import * as React from 'react';
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';

function appBar() {
    return (
        <Box sx={{ flexGrow: 1 ,  padding:'0'}}>
            <AppBar position="static" sx={{borderBottom:'1px solid', borderColor:' #484848' ,boxShadow:'none', padding:'0'}}>
                <Toolbar sx={{ justifyContent: 'space-between', bgcolor: 'white', color: 'black', padding:'0' }}>
                    <Typography
                        color="#1976d2"
                        sx={{ mr: 5 }}
                    >
                        SUBSCRIBE
                    </Typography>
                    <Typography fontSize={23} component="h3">
                        Blog
                    </Typography>
                    <Button color="inherit" variant='outlined' sx={{ border: '1px solid #1976d2', color: '#1976d2' }}>Sign up</Button>
                </Toolbar>
            </AppBar>
        </Box>
    );
}

export default appBar