import * as React from 'react';
import Card from '@mui/material/Card';
import CardActions from '@mui/material/CardActions';
import CardContent from '@mui/material/CardContent';
import Typography from '@mui/material/Typography';
import { ImageListItem } from '@mui/material';
import { Box } from '@mui/system';

function listImg() {
    return (
        <Box justifyContent={'spaceBetween'} sx={{ display: 'flex', marginTop: '32px' }}>
            <Card sx={{ width: 345, height: '240px', display: 'flex', justifyContent: 'space-between', paddingTop: '20px', width: '592px' }}>

                <Box>
                    <CardContent>
                        <Typography gutterBottom variant="h5" component="div">
                            Lizard
                        </Typography>
                        <Typography fontSize={14} component="p">Nov 12</Typography>
                        <Typography variant="body2" color="text.secondary">
                            This is a wider card with supporting text below as a natural lead-in to additional content.
                        </Typography>
                    </CardContent>
                    <CardActions>
                        <Typography sx={{ color: '#1976d2' }} p={1} size="small">Continue reading...</Typography>
                    </CardActions>
                </Box>
                <ImageListItem
                    component="img"
                    src='https://source.unsplash.com/random'
                    width="160"
                    height="240"
                />
            </Card>
            <Card sx={{ width: 345, height: '240px', display: 'flex', justifyContent: 'space-between', paddingTop: '20px', width: '592px' }}>

                <Box>
                    <CardContent>
                        <Typography gutterBottom variant="h5" component="div">
                            Lizard
                        </Typography>
                        <Typography fontSize={14} component="p">Nov 12</Typography>
                        <Typography variant="body2" color="text.secondary">
                            This is a wider card with supporting text below as a natural lead-in to additional content.
                        </Typography>
                    </CardContent>
                    <CardActions>
                        <Typography sx={{ color: '#1976d2' }} p={1} size="small">Continue reading...</Typography>
                    </CardActions>
                </Box>
                <ImageListItem
                    component="img"
                    src='https://source.unsplash.com/random'
                    width="160"
                    height="240"
                />
            </Card>
        </Box>
    );
}

export default listImg