import * as React from 'react';
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';

function appBar() {
    return (
        <Box sx={{ flexGrow: 1 , mt:'1px'}}>
            <Toolbar sx={{ justifyContent: 'space-between', bgcolor: 'white', color: 'black' }}>
                <Typography fontSize={15} sx={{ textDecoration: 'underline' }}>Technology</Typography>

                <Typography fontSize={15} sx={{ textDecoration: 'underline' }}>Design</Typography>

                <Typography fontSize={15} sx={{ textDecoration: 'underline' }}>Culture</Typography>

                <Typography fontSize={15} sx={{ textDecoration: 'underline' }}>Business</Typography>

                <Typography fontSize={15} sx={{ textDecoration: 'underline' }}>Politics</Typography>

                <Typography fontSize={15} sx={{ textDecoration: 'underline' }}>Opinion</Typography>

                <Typography fontSize={15} sx={{ textDecoration: 'underline' }}>Science</Typography>

                <Typography fontSize={15} sx={{ textDecoration: 'underline' }}>Health</Typography>

                <Typography fontSize={15} sx={{ textDecoration: 'underline' }}>Style</Typography>

                <Typography fontSize={15} sx={{ textDecoration: 'underline' }}>Travel</Typography>

            </Toolbar>
        </Box>
    );
}

export default appBar