import * as React from 'react';
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';

function appBar() {
    return (
        <Box component='a' href='#' justifyContent={'center'}  sx={{ pl:'auto' , flexGrow: 1, backgroundImage: 'https://source.unsplash.com/random', position: 'static', }}>
            <Box sx={{width:'1100px'}} >
                <Box  ml={1} sx={{ backgroundImage: 'url(https://source.unsplash.com/random)', height: '360px', width: '100%',backgroundRepeat:'no-repeat',marginLeft:'30px'}} />
            </Box>
            <Box sx={{ justifyContent: 'space-between', bgcolor: 'inherit', color: 'white', width: '570px', zIndex: '5', position: 'absolute', top: '170px', left: ' 260px' }}>
                <Typography fontSize={49} component='h2'>Title of a longer featured blog post</Typography>

                <Typography fontSize={24} component="p">Multiple lines of text that form the lede, informing new readers quickly and efficiently about what's most interesting in this post's contents.</Typography>
                <Typography sx={{color:'#1976d2', padding:'16px'}} component='h2'>Continue reading…</Typography>
            </Box>
        </Box>
    );
}

export default appBar